body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/static/media/backlit-bird-clouds-755385.65283243.jpg);
  background-repeat: round;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app{
  
}
.header{
    height: 0vh;
}

.header .links{
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    list-style: none;
    padding: 0px;
}

.header .links span{
    color: black;
    font-weight: bold;
}

.header .links .logo{
    width: 3em;
    height: 3em;
}

.header .links .spacer{
    flex: 1 1 auto;
}

.header .links .settings-button{
    background-color: rgba(0,0,0,0);
}

/*.header .links a:hover{
    background: rgb(89, 5, 168);
}*/
.settings{
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
}

.settings .settings-form{
    width: 50%;
}

.settings .settings-form .form-name{
    color: white;
    background-color: black;
    display: flex;
    justify-content: center;
    font-weight: bold;
    padding: 1em;
    border-radius: 4px;
    margin-bottom: 30px;
}

.settings .settings-form .form .button-save{
    display: flex;
    justify-content: flex-end;
}

@media only screen and (max-width: 600px) {
    .settings .settings-form{
        width: 100%;
    }
}

.applications{
    height: 100vh;
    margin-top: 10% !important;
}

.applications .test-status{
    display: flex;
    justify-content: flex-end;
}

.applications .test-status .button-status{
    background-color: black;
    color: white;
    margin-bottom: 5%;
    margin-right: 0px !important;
}

@media only screen and (max-width: 600px) {
    .applications{
        margin-top: 20% !important;
    }
}
.ui.divided.list>.item{
    border-top: 1px solid rgba(34,36,38,0.55) !important;
}

.ui.divided.list .item .content .header{
    margin-bottom: 2%;
    color: white !important;
}

.ui.divided.list>.item:first-child{
    border-top: none !important;
}

@media only screen and (max-width: 600px){
    .ui.divided.list .item .content .header{
        margin-bottom: 5%;
    }
}
.center-text{
    text-align: center;
}

.login-main{
    margin-top: 15% !important;
    width: 40% !important;
}

.login-header{
    background-color: black;
    color: #fff;
    padding: 5px;
    border-radius: 4px;
}

